// mixin
import saleCommon from '../mixins/saleCommon'
export default {
  name: 'help-Report',
  mixins : [saleCommon],
  data() {
    return {
      areaList: [],
      areaValue: [],
      bankList: [],
      bankValue: []
    };
  },
  methods: {
    end() {
      if (!this.btnStatus) return;
      if (this.areaValue.length == 0 || this.areaValue.indexOf('') === 0) {
        this.$vux.alert.show({
          title: "",
          content: ""
        });
        return;
      } else if (this.bankValue.length == 0) {
        this.$vux.alert.show({
          title: "",
          content: ""
        });
        return;
      } else if (this.helpCaptain == "") {
        this.$vux.alert.show({
          title: "",
          content: ""
        });
        return;
      } else if (this.arr[0].value.length == 0) {
        this.$vux.alert.show({
          title: "",
          content: ""
        });
        return;
      } else if (!this.governor) {
        this.$vux.alert.show({
          title: "",
          content: ""
        });
        return;
      } else if (!this.member) {
        this.$vux.alert.show({
          title: "",
          content: ""
        });
        return;
      }

      localStorage.setItem(
        "HELP_ZONE_INFO",
        JSON.stringify({
          bank: this.bankValue[0],
          areaValue: this.areaValue[0],
          captain: this.helpCaptain
        })
      );

      let governor = this.governor,
          member = this.member,
          comments = this.comments,
          HELP_ZONE_INFO = JSON.parse(localStorage.getItem("HELP_ZONE_INFO")),
          ROSE_OPTION = JSON.parse(localStorage.getItem("ROSE_OPTION"));  //

      // 
      let jsonData = {
        listId: "4bda3e47-a088-4749-a988-ebb07cfb00e4",
        referenceId: this.guid(),
        baseinfoExt: {
          id: this.guid(),
          varchar1: ROSE_OPTION.dept,         //() ()
          varchar2: HELP_ZONE_INFO.captain,   //
          varchar3: HELP_ZONE_INFO.areaValue, //()
          varchar4: HELP_ZONE_INFO.bank,      //
          varchar5: ROSE_OPTION.groupName,    //()
          varchar6: "",                     //
          varchar7: governor,                 //
          varchar8: member,                   //
          varchar9: comments,                 //
          varchar10: ROSE_OPTION.region,     //()
          varchar11: ROSE_OPTION.bank,        //()
          varchar12: ROSE_OPTION.userCode     //

        },
        transDetailUncalc: [],
        transCode: "XHXSDD"
      };
      // 
      for (let i = 0; i < this.arr.length; i++) {
        if (this.arr[i].value[0] != "" && this.arr[i].qty === "") {
          this.$vux.alert.show({
            title: "",
            content: ""
          });
          return;
        } else {
          jsonData.transDetailUncalc.push({
            id: this.guid(),
            transObjCode:this.arr[i].value[0] === "" ? "" : this.arr[i].value[0].split("_")[0], //
            containerCode: "", //
            qty: this.arr[i].value[0] === "" ? "" : this.arr[i].qty,
            taxAmount: this.arr[i].value[0] === "" ? "" : Number(this.arr[i].value[0].split("_")[2]),
            amount: this.arr[i].qty * this.arr[i].value[0].split('_')[3], //
            fgCode: ""
          });
        }
      }
      if (!this.Aclass) {
        this.$vux.alert.show({
          title: "",
          content: "A"
        });
        return;
      } else if (!this.Bclass) {
        this.$vux.alert.show({
          title: "",
          content: "B"
        });
        return;
      } else {
        jsonData.transDetailUncalc.push(
          {
            id: this.guid(),
            transObjCode: "A", //
            containerCode: "A", //
            qty: "",
            amount: Number(this.Aclass), //
            fgCode: ""
          },
          {
            id: this.guid(),
            transObjCode: "B", //
            containerCode: "B", //
            qty: "",
            amount: Number(this.Bclass), //
            fgCode: ""
          }
        );
      }
      let totalInfo = {
        isMobile: true,
        conn: 20000,
        list: "trans_form_data",
        transCode: "XHXSDD",
        jsonData: JSON.stringify(jsonData)
      };
      this.totalInfo = totalInfo;

      localStorage.setItem(
        "saleReportInfo",
        JSON.stringify({
          saleReportRemark: totalInfo,
          time: new Date().getTime()
        })
      );
      localStorage.setItem(
        "help_saleReport",
        JSON.stringify({
          saleReportArr: this.arr,
          Aclass: this.Aclass,
          Bclass: this.Bclass,
          time: new Date().getTime()
        })
      );

      localStorage.setItem(
        "HELP_INFO_LIST",
        JSON.stringify({
          captain: this.helpCaptain,
          governor: this.governor,
          member: this.member
        })
      );
      this.$router.push({ path: "/count" });
    }
  },
  created(){
    this.getArea();
    this.getBank();
    if (localStorage.getItem("HELP_INFO_LIST")) {
      this.helpCaptain = JSON.parse(
        localStorage.getItem("HELP_INFO_LIST")
      ).captain;
      this.governor = JSON.parse(
        localStorage.getItem("HELP_INFO_LIST")
      ).governor;
      this.member = JSON.parse(localStorage.getItem("HELP_INFO_LIST")).member;
    }  
  },
  mounted() {
    if (localStorage.getItem("help_saleReport")) {
      this.arr = JSON.parse(
        localStorage.getItem("help_saleReport")
      ).saleReportArr;
      this.Aclass = JSON.parse(localStorage.getItem("help_saleReport")).Aclass;
      this.Bclass = JSON.parse(localStorage.getItem("help_saleReport")).Bclass;
    }
    if (localStorage.getItem("HELP_ZONE_INFO")) {
      this.areaValue = [
        JSON.parse(localStorage.getItem("HELP_ZONE_INFO")).areaValue
      ];
      this.bankValue = [
        JSON.parse(localStorage.getItem("HELP_ZONE_INFO")).bank
      ];
    }

  },
  beforeRouteLeave(to, from, next) {
    let that = this;
    if (that.arr[0].value.length == 0 || to.name == "Count") {
      next();
    } else {
      that.$vux.confirm.show({
        title: "",
        content: "",
        confirmText: "",
        cancelText: "",
        onCancel() {
          localStorage.removeItem("help_saleReport");
          localStorage.removeItem("HELP_INFO_LIST");
          next();
        },
        onConfirm() {
          //
          localStorage.setItem(
            "help_saleReport",
            JSON.stringify({
              saleReportArr: that.arr,
              Aclass: that.Aclass,
              Bclass: that.Bclass,
              captain: that.helpCaptain,
              time: new Date().getTime()
            })
          );
          //
          localStorage.setItem(
            "HELP_ZONE_INFO",
            JSON.stringify({
              bank: that.bankValue[0],
              areaValue: that.areaValue[0],
              captain: that.helpCaptain
            })
          );
          //
          localStorage.setItem(
            "HELP_INFO_LIST",
            JSON.stringify({
              captain: that.helpCaptain,
              governor: that.governor,
              member: that.member
            })
          );
          next();
        }
      });
    }
  }
};



